module.exports={
  text: `March 29, 2019

Rachael English
Director of Training and People
EF Explore America
8 Education Street
Cambridge, MA 02141


Ms. English,

I came across the Developer Intern opportunity on your company’s website. After viewing the position, requirements, and goals of Education First, I would be privileged to grow both the company’s impact and my skills as a developer. Opportunity and education are, what I believe, can make a life worth living, and EF offers both to its clients and employees. I am sure my experience and knowledge, along with my ambition, will be a valuable addition to the team. 

My path to becoming a web developer started when I was a teenager. I first started playing with static HTML pages and bash scripts. Since then I have studied and worked with many other languages, libraries, and paradigms. When I find a new technology, the urge to read or watch videos is always in my head, turning into something that keeps me up while I try to understand it.

While in college, studying Computer Science, I worked full time as a shift leader at a local restaurant from which I gained critical soft skills and awesome friendships. When not busy with school or work I occupied myself with learning. In an effort to save years off my education, I attended Ironhack in Miami. Ironhack is an intensive program that aims to teach web development in an accelerated course. It was an invaluable decision, and now I am much closer to starting a career in the field I have wanted to for so long.

My resume is attached, and I encourage you to look at it. If you wish to contact me to discuss the position, please do not hesitate.

Best regards,

Cameron Chromiak
978 660 8870
cameron.chromiak@gmail.com

`
}
